import React, { Component } from 'react';
import {ProductConsumer} from '../context';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import ButtonContainer from './Button';

export default class Details extends Component {
    render() {
        return (
          <ProductConsumer>
              {(value)=> {
                 const {id, company, img, info, price, title, inCart} = value.detailProduct;
                 
                 return (
                    <div className= 'container py-5'>
                        <div className='row'>
                            <div className='col-10 mx-auto text-center text-slanted text-blue my-5'>
                                <h1>{title}</h1>

                            </div>
                        </div>
                        {/*product information */}
                        <div className='row'>
                            <div className='col-10 mx-auto col-md-6 my-3 text-capitalize'>
                                <img src={img} className='img-fluid' alt='product'/>
                                 
                            </div>
                            <div className='col-10 mx-auto col-md-6 my-3 text-capitalize'>
                                 <h2>Model: {title}</h2>
                                 <h4 className='text-title text-uppercase text-muted mt-3 mb-2'>
                                     manufactuer: <span className='text-uppercase'>{company}</span>
                                 </h4>
                                 <h4 className='text-blue'><strong>
                                     price: <span>$</span> {price}
                                     </strong></h4>
                                     <p className='text-capitalize font-weight-bold mt-3 mb-0'>
                                         Product info: 
                                     </p>
                                     <p className='text-muted lead'>{info}</p>
                                     <div >
                                         <Link to='/'>
                                            <ButtonContainer>
                                            <i class="fa fa-arrow-left" aria-hidden="true"></i> Back
                                            </ButtonContainer>
                                         </Link>

                                         <ButtonContainer cart={true} disabled={inCart ? true : false} onClick={() => {
                                             value.addToCart(id);
                                         }} >
                                             {inCart ? 'in cart' : <span>Add to cart <i class="fa fa-shopping-cart" aria-hidden="true"></i></span>}
                                         </ButtonContainer>

                                     </div>
                                 
                                 </div>
                        </div>
                    </div>
                 )
              }}
          </ProductConsumer>
        )
    }
}
